<template>
    <div class="container">
        <h1>Formulario de Conductores</h1>

        <form id="driver-form" @submit.prevent="guardar">

            <div class="form-group">
                <label for="cedula">Cedula:</label>
                <input type="text" id="cedula" name="cedula" required v-model="cedula">
            </div>

            <div class="form-group">
                <label for="nombre">Nombre:</label>
                <input type="text" id="nombre" name="nombre" required v-model="nombre">
            </div>

            <div class="form-group">
                <label for="apellido">Apellido:</label>
                <input type="text" id="apellido" name="apellido" required v-model="apellido">
            </div>

            <div class="form-group">
                <label for="telefono">Telefono:</label>
                <input type="text" id="telefono" name="telefono" required v-model="telefono">
            </div>

            <div class="form-group">
                <label for="direccion">Direccion:</label>
                <input type="text" id="direccion" name="direccion" required v-model="direccion">
            </div>

            <button type="submit" id="guardar">Guardar</button><br>
            <button type="button" id="eliminar" name="eliminar" @click="eliminar">Eliminar</button><br>
            <button type="button" id="actualizar" name="actualizar" @click="actualizar">Actualizar</button><br>
            <button type="button" id="consultar" name="consultar" @click="consultar">Consultar</button><br>
        </form>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            cedula: "",
            nombre: "",
            apellido: "",
            telefono: "",
            direccion: "",
        };
    },

    methods: {
        guardar() {
            axios
                .post("https://andenautos-production.up.railway.app/api/conductores", {
                    cedula: this.cedula,
                    nombre: this.nombre,
                    apellido: this.apellido,
                    telefono: this.telefono,
                    direccion: this.direccion,
                })
                .then((response) => {
                    console.log("Conductor registrado con éxito", response.data);
                    alert("Éxito");
                    this.cedula = "";
                    this.nombre = "";
                    this.apellido = "";
                    this.telefono = "";
                    this.direccion = "";
                    this.$emit("actualizar-tabla");
                })
                .catch((error) => {
                    console.error("Error al registrar conductor", error);
                });
        },

        consultar() {
            axios
                .get("https://andenautos-production.up.railway.app/api/conductores/" + this.cedula)
                .then((response) => {
                    this.nombre = response.data.nombre;
                    this.apellido = response.data.apellido;
                    this.telefono = response.data.telefono;
                    this.direccion = response.data.direccion;
                })
                .catch((error) => {
                    console.error("Error al consultar un conductor", error);
                });
        },

        actualizar(){

            axios
            .put("https://andenautos-production.up.railway.app/api/conductores/actualizar/" +this.cedula,{

            cedula:this.cedula,
            nombre:this.nombre,
            apellido:this.apellido,
            telefono:this.telefono,
            direccion:this.direccion,

            })
            .then ((response)=>{
                alert("Conductor actualizado con exito")
                console.log("Conductor actualizado con exito",response.data);
                this.cedula = "";
                    this.nombre = "";
                    this.apellido = "";
                    this.telefono = "";
                    this.direccion = "";

            })
            .catch((error)=>{

                console.error("Error al actualizar conductor",error);
            

            });
        },

        eliminar(){

            axios
            .delete("https://andenautos-production.up.railway.app/api/conductores/" + this.cedula)

            .then(()=>{

                alert("Conductor eliminado")
                console.log("Conductor eliminado con exito");

                    this.cedula ="";
                    this.nombre ="";
                    this.apellido ="";
                    this.telefono ="";
                    this.direccion ="";
                    this.$emit("actualizar-tabla");
                    
            })
                 .catch((error)=>{
                console.error("Falla al eliminar conductor",error)

                });

            },
        },
    };
</script>
