<template>
    
    <div class="container">

        <h1>Tabla de Conductores</h1>
        <table>
            <thead>
                <tr>
                    <th>Cedula</th>
                    <th>Nombre</th>
                    <th>Apellido</th>
                    <th>Telefono</th>
                    <th>Direccion</th>
                </tr>         
            </thead>

            <tbody>
                <tr v-for="conductor in conductores" :key="conductor.cedula">

                    <td>{{conductor.cedula}}</td>
                    <td>{{conductor.nombre}}</td>
                    <td>{{conductor.apellido}}</td>
                    <td>{{conductor.telefono}}</td>
                    <td>{{conductor.direccion}}</td>

                </tr>


            </tbody>
        </table>
    </div>

</template>

<script>

    import axios from "axios";

    export default{
        
        data(){

            return{
                conductores: [],
 
             };
        },

        methods:{

            obtenerConductores(){

                axios
                .get("https://andenautos-production.up.railway.app/api/conductores/listar")
                    .then((response)=>{

                        this.conductores=response.data;

                    })
                    .catch((error)=>{
                        console.error("Error al listar los conductores",error);

                    });
                              
            },
        },

        mounted(){
            this.obtenerConductores();

        },
    };




</script>








